import React from 'react';
import './style.css';
import Header from '../../Utils/Header';
import pencilImage from '../../images/pencil1.svg';
import TitleHolder from '../../Utils/TitleHolder';
import DatePicker from '../../Utils/DatePicker';
import handleErrors from '../../Utils/functions/handleErrors';
import { connect } from 'react-redux';
import TextArea from '../../Utils/TextArea';
import Footer from '../../Utils/Footer';
import { Redirect } from 'react-router-dom';
import BaseForm from '../../Utils/BaseForm';
import axios from 'axios';
import Select from 'react-select';
import ProgressBar from 'react-progress-bar-plus';
import defaultEventImage from '../../images/defaultEvent.png';
import NumberConverter from '../../Utils/BaseForm/numberConverter';
import { toast } from 'react-toastify';
import ReactLoading from 'react-loading';
import NotFound from '../NotFound';
import Popup from 'reactjs-popup';

function mapStateToProps(state) {
  return {
    authenticated: state.authenticated,
    user: state.user
  };
}

class CreateEvent extends BaseForm {
  constructor(props) {
    super(props);
    this.state = {
      title: '',
      location: '',
      beginTime: '',
      endTime: '',
      description: '',
      capacity: '',
      p_capacity: '',
      organizer: this.props.user.username,
      redirect: false,
      image: defaultEventImage,
      file: null,
      loading: true,
      sponsers: [],
      staffs: [],
      sponserSelected: [],
      staffSelected: [],
      sponserText: '',
      sponserSite: '',
      staffText: '',
      warnings: [],
      isEditing: false,
      isUploading: false,
      isEdited: false,
      isUploaded: true,
      isOwner: false
    };

    this.handleBeginTime = this.handleBeginTime.bind(this);
    this.handleEndTime = this.handleEndTime.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleDescription = this.handleDescription.bind(this);
    this.onChange = this.onChange.bind(this);
    this.fileUpload = this.fileUpload.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
  }
  onChange(event) {
    event.preventDefault();
    let reader = new FileReader();
    let file = event.target.files[0];
    reader.onloadend = () => {
      this.setState({
        file: file,
        image: reader.result
      });
    };
    reader.readAsDataURL(file);
  }

  handleDescription(description) {
    this.setState({ description: description });
  }
  async fileUpload(id, token) {
    this.setState({ isUploading: true, isUploaded: false });
    const url = '/api/v1/event/upload/' + id;
    let data = await new FormData();
    data.append('event', this.state.file, this.state.file.name);
    let config = {
      headers: {
        authorization: token
      },
      params: {
        username: this.state.organizer
      }
    };
    axios
      .post(url, data, config)
      .then(() => {
        this.setState({ isUploading: false, isUploaded: true });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  handleSubmit() {
    this.setState({ isEditing: true });

    let that = this;
    let data = {
      title: that.state.title,
      location: that.state.location,
      beginTime: that.state.beginTime,
      endTime: that.state.endTime,
      description: that.state.description,
      organizer: that.state.organizer,
      sponsers: that.state.sponserSelected,
      staffs: that.state.staffSelected,
      capacity: that.state.capacity
    };
    const token = localStorage.getItem('accessToken');
    const method = this.props.type === 'create' ? 'POST' : 'PUT';
    let id = this.props.type === 'create' ? '' : this.props.match.params.id;
    let url = `/api/v1/event/${id}`;
    let dataSend = {
      method: method,
      headers: {
        'Content-Type': 'application/json',
        authorization: token
      },
      body: JSON.stringify({ data: data })
    };
    fetch(url, dataSend)
      .then(function(response) {
        if (that.props.type === 'create') return response.json();
        else return response;
      })
      .then(ress => {
        if (that.props.type === 'create') {
          id = ress.data;
        }
        if (this.state.file != null) {
          this.fileUpload(id, token);
        }
        if (!ress.hasOwnProperty('errors')) {
          if (this.props.type === 'create') {
            toast.success('رویداد شما با موفقیت ساخته شد!');
          } else {
            toast.success('رویداد شما با موفقیت ویرایش شد!');
          }
          that.setState({ isEdited: true });
        }
        return ress;
      })
      .then(handleErrors)
      .catch(function(error) {
        console.log(error);
      });
  }

  componentDidMount() {
    let that = this;
    if (this.props.type === 'create') {
      this.setState({ loading: false });
      this.setState({ isOwner: true });
      return;
    } else {
      const id = this.props.match.params.id;

      fetch(`/api/v1/event/${id}`)
        .then(function(response) {
          return response.json();
        })
        .then(handleErrors)
        .then(function(responseJson) {
          return responseJson.data;
        })
        .then(function(info) {
          if (that.props.user.username === info.event.organizer) {
            that.setState({ isOwner: true });
          } else {
            that.setState({ isOwner: false });
          }
          that.setState({
            title: info.event.title,
            location: info.event.location,
            description: info.event.description,
            beginTime: info.event.beginTime,
            endTime: info.event.endTime,
            image: info.event.image,
            capacity: info.event.capacity,
            p_capacity: NumberConverter.toPersian(String(info.event.capacity))
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    }

    this.setState({ loading: false });
  }

  handleBeginTime(day, month, year) {
    let formattedDay = day < 10 ? (day = '0' + day) : day;
    let formattedMonth = month < 10 ? (month = '0' + month) : month;
    let beginTime = year + '-' + formattedMonth + '-' + formattedDay;

    this.setState({ beginTime: beginTime });
  }

  handleEndTime(day, month, year) {
    let formattedDay = day < 10 ? (day = '0' + day) : day;
    let formattedMonth = month < 10 ? (month = '0' + month) : month;
    let endTime = year + '-' + formattedMonth + '-' + formattedDay;

    this.setState({ endTime: endTime });
  }

  handleSelect = name => optionSelected => {
    const value = optionSelected.map(option => option.value);
    this.setState({ [name]: value });
  };

  onInputChange = name => newText => {
    let that = this;
    let token = localStorage.getItem('accessToken');
    let stateName = name === 'user' ? 'staffs' : 'sponsers';
    let textName = name === 'user' ? 'staffText' : 'sponserText';
    this.setState({ [textName]: newText });
    fetch(`/api/v1/${name}/search/${newText}`, {
      headers: {
        authorization: token
      }
    })
      .then(function(response) {
        return response.json();
      })
      .then(handleErrors)
      .then(function(responseJson) {
        return responseJson.data;
      })
      .then(function(info) {
        let usernames = info.map(item => {
          let obj = {
            value: item,
            label: name === 'user' ? item.username : item.name
          };
          return obj;
        });
        that.setState({ [stateName]: usernames });
      })
      .catch(function(error) {
        console.log(error);
      });
  };

  handleAddSponser = () => {
    let token = localStorage.getItem('accessToken');
    let data = {
      name: this.state.sponserText,
      url: this.state.sponserSite
    };
    if (data.name.length === 0) {
      toast.error('نام حامی خالی است!');
      return;
    }
    fetch('/api/v1/sponser', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: token
      },
      body: JSON.stringify({
        data: data
      })
    }).catch(function(error) {
      console.log(error);
    });
    toast.success('حامی جدید با موفقیت ساخته شد');
  };

  handleSponserName = e => {
    this.setState({ sponserText: e.target.value });
  };

  handleSponserAddress = e => {
    this.setState({ sponserSite: e.target.value });
  };

  render() {
    if (!this.state.isOwner) {
      return <NotFound />;
    }
    if (this.state.isUploaded && this.state.isEdited) {
      if (this.props.type === 'create') {
        return <Redirect to="/my-events" />;
      } else {
        const id = this.props.match.params.id;
        return <Redirect to={`/event/${id}`} />;
      }
    }

    const contentStyle = {
      height: 'innerHeight',
      width: 'innerWidth',
      'z-index': '1',
      padding: '0px'
    };

    const innerDiv = {
      background: '#000000cc',
      'z-index': '0'
    };

    const addSponser = (
      <Popup
        trigger={<p className="create_event_add_sponser">+</p>}
        modal
        closeOnDocumentClick
        contentStyle={contentStyle}
        overlayStyle={innerDiv}
      >
        {close => (
          <div className="create_event_sponser_modal">
            <form>
              <div className="create_event_sponser_modal_name">
                <p> نام حامی: </p>
                <input name="name" onChange={this.handleSponserName} required />
              </div>
              <div className="create_event_sponser_modal_address">
                <p> آدرس سایت: </p>
                <input
                  name="address"
                  onChange={this.handleSponserAddress}
                  required
                />
              </div>
              {/* <div className="create_event_sponser_modal_picture">
                <img src={defaultEventImage} alt={'عکس حامی'} />
                <label className="modal_change_picture" htmlFor="upload-photo">
                  {' '}
                  تغییر تصویر{' '}
                </label>
                <input
                  type="file"
                  name="modal-upload"
                  id="upload-photo"
                  onChange={this.onChange}
                />
              </div> */}
              <div
                className="create_event_sponser_modal_submit"
                onClick={event => {
                  this.handleAddSponser();
                  close();
                }}
              >
                <p> ثبت </p>
              </div>
            </form>
          </div>
        )}
      </Popup>
    );
    return (
      <div className="container">
        <ProgressBar
          percent={this.state.loading ? 0 : 100}
          spinner={false}
          autoIncrement={true}
        />
        <Header />
        <div className="create_event_container1">
          <div className="create_event_title">
            <div className="create_event_title_container">
              <TitleHolder
                title={
                  this.props.type === 'create' ? 'ساخت رویداد' : 'ویرایش رویداد'
                }
                image={pencilImage}
                customHeight="42px"
              />
            </div>
          </div>

          <div className="create_event_container2">
            <div className="create_event_picture">
              <div className="create_event_tittle">
                <p className="create_event_header_font"> تصویر رویداد </p>
              </div>
              <div className="create_event_picture_content">
                <div className="prof_pic">
                  {this.state.image === '' ? (
                    <img src={defaultEventImage} alt="عکس رویداد" />
                  ) : (
                    <img src={this.state.image} alt="عکس رویداد" />
                  )}
                </div>
                <label className="change_button" htmlFor="upload-photo">
                  {' '}
                  تغییر تصویر{' '}
                </label>
                <input type="file" id="upload-photo" onChange={this.onChange} />
              </div>
            </div>
            <div className="create_event_details">
              <div className="create_event_tittle">
                <p className="create_event_header_font"> مشخصات رویداد </p>
              </div>
              <div className="create_event_details_content">
                <div className="create_event_details_row first_row">
                  <div className="create_event_input left_field">
                    <p className="create_event_subtittle_font"> نام رویداد: </p>
                    <input
                      className="create_event_rest_input"
                      name="title"
                      type="text"
                      onChange={this.handleChange}
                      value={this.state.title}
                      maxLength="32"
                    />
                  </div>
                  <div className="create_event_input right_field">
                    <p className="create_event_subtittle_font">
                      {' '}
                      محل برگزاری:{' '}
                    </p>
                    <input
                      className="create_event_rest_input"
                      name="location"
                      type="text"
                      onChange={this.handleChange}
                      value={this.state.location}
                      maxLength="32"
                    />
                  </div>
                </div>
                <div className="create_event_details_row second_row">
                  <div className="create_event_input">
                    <p className="create_event_subtittle_font"> تاریخ شروع: </p>
                    <DatePicker
                      date={this.state.beginTime}
                      handleTime={this.handleBeginTime}
                    />
                  </div>
                  <div className="create_event_input">
                    <p className="create_event_subtittle_font">
                      {' '}
                      تاریخ پایان:{' '}
                    </p>
                    <DatePicker
                      date={this.state.endTime}
                      handleTime={this.handleEndTime}
                    />
                  </div>
                  <div className="create_event_input">
                    <p className="create_event_subtittle_font"> ظرفیت: </p>
                    <div className="capacity_container">
                      <input
                        className="create_event_rest_input"
                        name="capacity"
                        type="text"
                        onChange={this.handleNumberInput}
                        value={this.state.p_capacity}
                        maxLength="6"
                      />
                    </div>
                  </div>
                </div>
                <div className="create_event_input about_center">
                  <p className="input_date"> توضیحات: </p>
                  <div className="create_event_textarea">
                    <TextArea
                      text={this.state.bio}
                      handleText={this.handleDescription}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="create_event_search">
              <div className="create_event_search_staff">
                <div className="create_event_tittle">
                  <p className="create_event_header_font"> اضافه کردن همکار </p>
                </div>
                <div className="create_event_search_content staff_search">
                  <div className="create_event_search_select">
                    <Select
                      options={this.state.staffs}
                      isMulti={true}
                      placeholder={'جستجو...'}
                      name="staffs"
                      onChange={this.handleSelect('staffSelected')}
                      onInputChange={this.onInputChange('user')}
                    />
                  </div>
                </div>
              </div>
              <div className="create_event_search_sponser">
                <div className="create_event_tittle">
                  <p className="create_event_header_font"> اضافه کردن حامی </p>
                </div>
                <div className="create_event_search_content sponser_search">
                  <div className="create_event_search_select">
                    <Select
                      options={this.state.sponsers}
                      isMulti={true}
                      placeholder={'جستجو...'}
                      name="sponsers"
                      onChange={this.handleSelect('sponserSelected')}
                      onInputChange={this.onInputChange('sponser')}
                      // noOptionsMessage={() => addSponser}
                    />
                  </div>
                  {addSponser}
                </div>
              </div>
            </div>
            <div
              className="uploading"
              style={
                this.state.isEditing && this.state.isUploading
                  ? {}
                  : { display: 'none' }
              }
            >
              <ReactLoading
                type="spinningBubbles"
                color="#352649"
                height={30}
                width={30}
              />
            </div>
            <div className="create_event_submit">
              <input
                className="event_page_signup_button"
                type="submit"
                onClick={this.handleSubmit}
                value="ثبت"
              />
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default connect(mapStateToProps)(CreateEvent);
