import React from 'react';
import './style.css';
import handleErrors from '../../Utils/functions/handleErrors';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import BaseForm from '../../Utils/BaseForm';
import Register from '../Register';
import ForgetPassword from '../ForgetPassword';

function mapStateToProps (state) {
  return {
    authenticated: state.authenticated,
    user: state.user
  };
}

class Login extends BaseForm {
  constructor (props) {
    super(props);

    this.state = {
      username: '',
      password: '',
      noAccount: false,
      forgotPassword: false
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleNoAccount = this.handleNoAccount.bind(this);
    this.handleForgotPassword = this.handleForgotPassword.bind(this);
  }

  handleSubmit (event) {
    event.preventDefault();
    const data = this.state;
    let that = this;

    fetch('/api/v1/user/login', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ data: data })
    })
      .then(function (response) {
        return response.json();
      })
      .then(handleErrors)
      .then(function (responseJson) {
        localStorage.setItem('accessToken', responseJson.data.accessToken);
        localStorage.setItem('refreshToken', responseJson.data.refreshToken);
        return fetch(`/api/v1/user/${data.username}`);
      })
      .then(function (response) {
        return response.json();
      })
      .then(function (responseJson) {
        that.props.dispatch({ type: 'SET_USER', user: responseJson.data.user });
      })
      .then(function () {
        toast.info('شما با موفقیت وارد شدید');
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  handleNoAccount () {
    this.setState({ noAccount: true });
  }

  handleForgotPassword () {
    this.setState({ forgotPassword: true });
  }

  render () {
    if (this.state.noAccount) {
      return (
        <div>
          <Register />
        </div>
      );
    } else if (this.state.forgotPassword) {
      return (
        <div>
          <ForgetPassword />
        </div>
      );
    }
    return (
      <div className="login_page">
        <div className="login-title">ورود</div>
        <form onSubmit={this.handleSubmit}>
          <div className="login_form">
            <div className="input_container">
              <input
                type="text"
                className="login-input"
                placeholder="نام کاربری"
                name="username"
                onChange={this.handleChange}
                required
                autoFocus
              />
              <i className="fa fa-user" />
            </div>
            <div className="input_container">
              <input
                type="password"
                className="login-input"
                placeholder="رمز عبور"
                name="password"
                onChange={this.handleChange}
                required
              />
              <i className="fa fa-unlock-alt" />
            </div>
            <input type="submit" value="ورود" className="login-button" />
            <p className="login-lost">
              <a onClick={this.handleForgotPassword}>
                {' '}
                رمز عبور خود را فراموش کرده اید؟{' '}
              </a>{' '}
              /<a onClick={this.handleNoAccount}> حساب کاربری ندارید؟ </a>
            </p>
          </div>
        </form>
      </div>
    );
  }
}

export default connect(mapStateToProps)(Login);
